/**
 * Global slider
 *
 * @module patterns/slider/slider
 * @author Web Development
 * @copyright City, University of London 2018
 */

import {
    createHTMLElement,
    removeClass,
    arraySlicer,
    toBool,
} from '../../util';
import aria from '../../aria-attributes';
const className = 'slider-v';

function launchSlider(slider) {
    const sliderChildren = [...slider.children],
        totalItems = sliderChildren.length,
        perPageTablet = 2, // if this needs to be customisable in the future then it could come from a data attribute with set default values
        perPageDesk = 3,
        maxTablet = Math.ceil(totalItems / perPageTablet),
        maxDesk = Math.ceil(totalItems / perPageDesk);

    const sliderObject = {
        default: {
            totalPages: totalItems,
            maxItem: 1,
            pageAttr: 'data-page',
            visAttr: 'data-visible',
            increment: 1,
        },
        tablet: {
            totalPages: maxTablet,
            maxItem: perPageTablet,
            pageAttr: 'data-pagetablet',
            visAttr: 'data-visibletablet',
            increment: perPageTablet,
        },
        desk: {
            totalPages: maxDesk,
            maxItem: perPageDesk,
            pageAttr: 'data-pagedesk',
            visAttr: 'data-visibledesk',
            increment: perPageDesk,
        },
    };

    if (totalItems < 2) {
        removeClass(slider, className, false);
        return true;
    }

    let sliderType = slider.getAttribute('data-type'),
        sliderControl = slider.getAttribute('data-control'),
        sliderProgress = slider.getAttribute('data-progress');

    if (!sliderType) {
        sliderType = 'default';
    }

    if (!sliderControl) {
        sliderControl = 'default';
    }

    if (!sliderProgress) {
        sliderProgress = 'default';
    }

    let wrapper = createHTMLElement('div', [
        { label: 'class', val: `slider-block slider-block--${sliderType}` },
    ]);

    slider.parentNode.insertBefore(wrapper, slider);
    wrapper.appendChild(slider);

    if (sliderProgress !== 'off' && sliderControl !== 'off') {
        let controlWrapper = createHTMLElement('div', [
            { label: 'class', val: 'slider-block__control' },
        ]);
        wrapper.appendChild(controlWrapper);
    }

    setVisibility(slider, sliderType, sliderObject);

    if (sliderProgress !== 'off') {
        createProgressTracker(slider, sliderType, sliderObject);
    }

    if (sliderControl !== 'off') {
        createSliderButton(slider, sliderControl, sliderType, sliderObject);
    }
}

function setVisibility(slider, sliderType, sliderObject) {
    Array.from(slider.getElementsByTagName('li')).forEach((el, i) => {
        el.setAttribute(sliderObject.default.pageAttr, `${i}`);

        if (i === 0) {
            el.setAttribute(sliderObject.default.visAttr, true);
        } else {
            el.setAttribute(sliderObject.default.visAttr, false);
        }
    });

    if (sliderType === 'tablet') {
        const el = [...slider.children];

        let tabletSlides = arraySlicer(el, sliderObject.tablet.maxItem);
        let deskSlides = arraySlicer(el, sliderObject.desk.maxItem);

        slicedArrayIterator(
            tabletSlides,
            sliderObject.tablet.pageAttr,
            sliderObject.tablet.visAttr
        );
        slicedArrayIterator(
            deskSlides,
            sliderObject.desk.pageAttr,
            sliderObject.desk.visAttr
        );
    }
}

function slicedArrayIterator(arr, pageAttr, visibilityAttr) {
    for (let i = 0; i < arr.length; i++) {
        let subArray = arr[i];

        for (let j = 0; j < subArray.length; j++) {
            subArray[j].setAttribute(pageAttr, `${i}`);

            if (i === 0) {
                subArray[j].setAttribute(visibilityAttr, 'true');
            } else {
                subArray[j].setAttribute(visibilityAttr, 'false');
            }
        }
    }
}

function createProgressTracker(slider, sliderType, sliderObject) {
    const wrapper = slider.parentNode.querySelector('.slider-block__control');

    let progress = createHTMLElement('div', [
        { label: 'class', val: 'slider-block__control__progress' },
    ]);

    wrapper.appendChild(progress);

    let firstPage = createHTMLElement('span', [
        { label: 'data-currentpage', val: true },
    ]);
    firstPage.innerText = '1';

    let separator = document.createElement('span');
    separator.innerText = '/';

    let lastPage = createHTMLElement('span', [
        { label: sliderObject.default.visAttr, val: true },
    ]);

    lastPage.innerText = sliderObject.default.totalPages;

    progress.appendChild(firstPage);
    progress.appendChild(separator);
    progress.appendChild(lastPage);

    if (sliderType === 'tablet') {
        let lastPageTablet = createHTMLElement('span', [
            { label: sliderObject.tablet.visAttr, val: true },
        ]);
        lastPageTablet.innerText = sliderObject.tablet.totalPages;

        let lastPageDesk = createHTMLElement('span', [
            { label: sliderObject.desk.visAttr, val: true },
        ]);
        lastPageDesk.innerText = sliderObject.desk.totalPages;

        progress.appendChild(lastPageTablet);
        progress.appendChild(lastPageDesk);
    }
}

function createSliderButton(slider, sliderControl, sliderType, sliderObject) {
    if (sliderControl === 'default') {
        let buttonWrapper = createHTMLElement('div', [
            { label: 'class', val: 'slider-block__control__buttons' },
        ]);

        const controlWrapper = slider.parentNode.querySelector(
            '.slider-block__control'
        );
        controlWrapper.appendChild(buttonWrapper);

        let buttonPrev = createButtonElement('fas fa-arrow-left prev', 'default', sliderObject);
        let buttonNext = createButtonElement('fas fa-arrow-right next', 'default', sliderObject);

        buttonNext.setAttribute(aria.label, 'next item');

        buttonNext.addEventListener('click', function() {
            defaultEvent(this, slider, 'default', sliderObject);
        });

        buttonPrev.addEventListener('click', function() {
            defaultEvent(this, slider, 'default', sliderObject);
        });

        buttonWrapper.appendChild(buttonPrev);
        buttonWrapper.appendChild(buttonNext);

        if (sliderType === 'tablet') {
            let buttonPrevTablet = createButtonElement('fas fa-arrow-left prev', 'tablet', sliderObject),
                buttonNextTablet = createButtonElement('fas fa-arrow-right next', 'tablet', sliderObject),
                buttonPrevDesk = createButtonElement('fas fa-arrow-left prev', 'desk', sliderObject),
                buttonNextDesk = createButtonElement('fas fa-arrow-right next', 'desk', sliderObject);

            buttonWrapper.appendChild(buttonPrevTablet);
            buttonWrapper.appendChild(buttonNextTablet);
            buttonWrapper.appendChild(buttonPrevDesk);
            buttonWrapper.appendChild(buttonNextDesk);
        }
    }
}

function createButtonElement(classname, type, sliderObject) {
    let value;

    if (classname.indexOf('prev') !== -1) {
        value = -1;
    } else {
        value = 1;
    }

    let button = createHTMLElement('button', [
        { label: 'class', val: classname },
        { label: 'type', val: 'button' },
        { label: 'data-next', val: value },
        { label: `${sliderObject[type].visAttr}`, val: true },
    ]);

    if (classname.indexOf('prev') !== -1) {
        button.setAttribute(aria.label, 'previous item');
        button.setAttribute('disabled', true);
    } else {
        button.setAttribute(aria.label, 'next item');
    }

    return button;
}

function defaultEvent(button, slider, type, sliderObject) {

    const currentSlide = slider.querySelector(
            `[${sliderObject[type].visAttr} = 'true']`
        ),
        nextPage = button.getAttribute('data-next'),
        nextSlide = slider.querySelector(
            '[' + sliderObject[type].pageAttr + '= "' + nextPage + '"]'
        ),
        page = parseInt(
            currentSlide.getAttribute(sliderObject[type].pageAttr)
        ),
        prevButton = button.parentNode.querySelector('.prev'),
        nextButton = button.parentNode.querySelector('.next'),
        counterEl = slider.parentNode.querySelector('span[data-currentpage]');

    let counterValue = parseInt(counterEl.innerText);

    currentSlide.setAttribute(`${sliderObject[type].visAttr}`, false);
    nextSlide.setAttribute(`${sliderObject[type].visAttr}`, true);

    if (button.classList.contains('next')) {
        button.setAttribute('data-next', page + 2);
        prevButton.setAttribute('data-next', page);
        counterValue++;
        counterEl.innerText = counterValue;

        if (page === 0) {
            prevButton.removeAttribute('disabled');
        } else if (page + 2 === parseInt(sliderObject[type].totalPages)) {
            nextButton.setAttribute('disabled', true);
        }
    } else {
        button.setAttribute('data-next', page - 2);
        nextButton.setAttribute('data-next', page);
        counterValue--;
        counterEl.innerText = counterValue;

        if (page === 1) {
            prevButton.setAttribute('disabled', true);
        } else if (page + 2 === parseInt(sliderObject[type].totalPages)) {
            nextButton.removeAttribute('disabled');
        }
    }
}

function tabletEvent(button, slider, sliderObject) {}

export default {
    launchFn: launchSlider,
    launchQuery: `.${className}`,
};
